<template>
    <section class="ui-container">
        <h1 class="title">ACTIONSHEET</h1>
        <div class="demo-item">
            <p class="demo-desc"></p>
        </div>
        <div class="ui-btn-wrap">
            <vf-button size="lg" @onClick="show=true">Actionsheet</vf-button>
        </div>
        <vf-actionsheet v-if="show" @close="show=false" title="">
            <button></button>
            <button class="del"></button>
        </vf-actionsheet>
    </section>
</template>

<script>
export default {
    data () {
        return {
            show: false
        }
    }
}
</script>



