import Vue from 'vue'
import Router from 'vue-router'
import store from '@/stores'

Vue.use(Router)
const router = new Router({
    routes: [{
        path: '/index',
        component: require('@/view/index.vue').default
    }, {
        path: '/com/button',
        component: require('@/view/button.vue').default
    }, {
        path: '/com/list',
        component: require('@/view/list.vue').default
    }, {
        path: '/com/tips',
        component: require('@/view/tips.vue').default
    }, {
        path: '/com/dialog',
        component: require('@/view/dialog.vue').default
    }, {
        path: '/com/actionSheet',
        component: require('@/view/actionSheet.vue').default
    }, {
        path: '/com/tab',
        component: require('@/view/tab.vue').default
    }, {
        path: '/com/form',
        component: require('@/view/form.vue').default
    }, {
        path: '/com/grid',
        component: require('@/view/grid.vue').default
    }, {
        path: '/com/panel',
        component: require('@/view/panel.vue').default
    }, {
        path: '/com/searchbar',
        component: require('@/view/search.vue').default
    }, {
        path: '/com/subscript',
        component: require('@/view/subscript.vue').default
    }, {
        path: '/com/more',
        component: require('@/view/more.vue').default
    }, {
        path: '*',
        redirect: '/index'
    }]
})
// simple history management
const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
    endTime = Date.now()
})
methods.forEach(key => {
    let method = router[key].bind(router)
    router[key] = function (...args) {
        isPush = true
        method.apply(null, args)
    }
})

router.beforeEach(function (to, from, next) {
    store.commit('updateLoadingStatus', false)
    const toIndex = history.getItem(to.path)
    const fromIndex = history.getItem(from.path)

    if (toIndex) {
        if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
            store.commit('updateDirection', 'forward')
        } else {
            // ios
            if (!isPush && (Date.now() - endTime) < 377) {
                store.commit('updateDirection', '')
            } else {
                store.commit('updateDirection', 'back')
            }
        }
    } else {
        ++historyCount
        history.setItem('count', historyCount)
        to.path !== '/' && history.setItem(to.path, historyCount)
        store.commit('updateDirection', 'forward')
    }

    if (/\/http/.test(to.path)) {
        let url = to.path.split('http')[1]
        window.location.href = `http${url}`
    } else {
        next()
    }
})

router.afterEach(function (to) {
    isPush = false
    store.commit('updateLoadingStatus', { isLoading: false })
})

export default router



