<template>
    <button
        class="ui-btn"
        :style="buttonStyle"
        :class="classes"
        :disabled="disabled"
        :type="actionType"
        @click="$emit('onClick')">
        <i class="weui-loading" v-if="showLoading"></i>
        <slot>{{ text }}</slot>
        <span v-if="type=='progress'" class="ui-btn-inner" :style="progressStyle"><span></span></span>
    </button>
</template>
<script>
export default {
    props: {
        type: String,
        disabled: {
            type: Boolean,
            default: false
        },
        size: String,
        progress: String,
        plain: Boolean,
        text: String,
        actionType: String,
        showLoading: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        noBorder () {
            return Array.isArray(this.gradients)
        },
        progressStyle () {
            if (this.progress) {
                return {
                    width: `${this.progress}%`
                }
            }
        },
        buttonStyle () {
            if (this.gradients) {
                return {
                    background: `linear-gradient(90deg, ${this.gradients[0]}, ${this.gradients[1]})`,
                    color: '#FFFFFF'
                }
            }
        },
        classes () {
            return [
                this.disabled ? 'disabled' : '',
                this.type ? `ui-btn-${this.type}` : '',
                this.size ? `ui-btn-${this.size}` : ''
            ]
        }
    }
}

</script>

<style lang="scss">
@import "../assets/component/button/btn.scss";
@import "../assets/component/button/btn-progress.scss";
@import "../assets/component/button/btn-group.scss";
</style>



