<template>
    <div id="app">
        <transition :name="viewTransition">
            <router-view></router-view>
        </transition>
    </div>
</template>
<script>
import store from '@/stores'
export default {
    name: 'App',
    computed: {
        direction () {
            return store.state.direction
        },
        viewTransition () {
            if (!this.direction) return ''
            return this.direction
        }
    }
}

</script>
<style>
    html, body {
        height: 100%;
        width: 100%;
        overflow: hidden;
    }
    #app {
        height: 100%;
        overflow: hidden;
        width: 100%;
    }
</style>



