<template>
    <li :class="classes">
        <slot></slot>
    </li>
</template>

<script>
export default {
    props: {
        border: String
    },
    computed: {
        classes () {
            return [
                this.border ? `ui-border-${this.border}` : ''
            ]
        }
    }
}
</script>



