<template>
    <ul class="ui-list"
        :class="classes">
        <slot></slot>
    </ul>
</template>
<script>
export default {
    props: {
        type: String,
        size: String,
        border: String
    },
    computed: {
        classes () {
            return [
                this.size ? `ui-list-${this.size}` : '',
                this.type ? `ui-list-${this.type}` : '',
                this.border ? `ui-border-${this.border}` : ''
            ]
        }
    }
}
</script>
<style lang="scss">
@import "../assets/component/list/list";
@import "../assets/component/list/list-avatar";
@import "../assets/component/list/list-function";
@import "../assets/component/list/list-thumb";
</style>



