<template>
    <transition name="dialog-fade">
        <div>
            <div class="ui-translucent-mask"></div>
            <div class="ui-dialog" :class="classes">
                <div class="ui-dialog-cnt">
                    <slot></slot>
                    <div class="ui-dialog-ft" v-if="showBottom">
                        <button @click="$emit('close')"></button>
                        <button @click="$emit('done')"></button>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
export default {
    props: {
        type: String,
        media: Boolean,
        operate: Boolean
    },
    computed: {
        showBottom () {
            return !this.operate
        },
        noBorder () {
            return Array.isArray(this.gradients)
        },
        progressStyle () {
            if (this.progress) {
                return {
                    width: `${this.progress}%`
                }
            }
        },
        buttonStyle () {
            if (this.gradients) {
                return {
                    background: `linear-gradient(90deg, ${this.gradients[0]}, ${this.gradients[1]})`,
                    color: '#FFFFFF'
                }
            }
        },
        classes () {
            return [
                this.operate ? 'ui-dialog-operate-icon' : '',
                this.operate ? 'ui-dialog-operate' : '',
                this.media ? 'ui-dialog-function' : ''
            ]
        }
    }
}

</script>

<style lang="scss">
@import "../assets/component/dialog/base";
@import "../assets/component/dialog/dialog";
.ui-dialog-bd {
    min-height: 46px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    padding: 18px 28px 18px 28px;
    display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-box-align: center;
    -webkit-box-orient: vertical;
}
.dialog-fade-leave-active {
    animation-duration: 500ms;
    position: relative;
    z-index: 9999;
}
.dialog-fade-leave-to{
    animation-name: bgFadeOut;
    .ui-dialog {
        animation-duration: 500ms;
        animation-name: contentZoomOut;
    }
}
</style>



