<template>
    <div class="ui-searchbar-wrap" :style="barStyle" :class="wrapClass">
        <div class="ui-searchbar ui-border-radius" :class="classes">
            <i class="ui-icon-search"></i>
            <div class="ui-searchbar-text">2-10</div>
            <div class="ui-searchbar-input"><input ref="input" value="" type="tel" placeholder="2-10" autocapitalize="off"></div>
            <i class="ui-icon-close"></i>
        </div>
        <button class="ui-searchbar-cancel"></button>
    </div>
</template>
<script>
export default {
    props: {
        bg: String,
        disabled: {
            type: Boolean,
            default: false
        },
        border: String
    },
    computed: {
        wrapClass () {
            return [
                this.border ? `ui-border-${this.border}` : ''
            ]
        },
        barStyle () {
            if (this.bg) {
                return {
                    background: `${this.bg}`,
                    color: '#FFFFFF'
                }
            }
        },
        classes () {
            return [
                this.bg ? 'ui-searchbar-light' : ''
            ]
        }
    }
}

</script>

<style lang="scss">
    @import "../assets/component/search/searchbar";
    .ui-searchbar-light {
        background: rgba(255, 255, 255, .1);
        &:before {
            border: none;
        }
        .ui-searchbar-text {
            color: rgba(255, 255, 255, .4);
        }
        .ui-icon-search {
            color: rgba(255, 255, 255, .4);
        }
    }
</style>



