<template>
    <div class="ui-subscript-wrap">
        <i class="ui-subscript" :class="classes">{{text}}</i>
        <slot></slot>
    </div>
</template>
<script>
export default {
    props: {
        color: {
            type: String,
            default: 'red'
        },
        text: {
            type: String,
            default: ''
        }
    },
    computed: {
        classes () {
            return [
                this.color ? `ui-subscript-${this.color}` : ''
            ]
        }
    }
}

</script>

<style lang="scss">
    @import "../assets/component/decorate/tag";
    @import "../assets/component/decorate/subscript";
    .ui-subscript-wrap {
        overflow:hidden;
        position: relative;
    }
</style>



