import './assets/util/util.scss'
import './assets/css/style.css'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './stores'

import button from '@/components/button.vue'
import actionsheet from '@/components/actionsheet.vue'
import cellGroup from '@/components/cell-group.vue'
import dialog from '@/components/dialog.vue'
import cell from '@/components/cell.vue'
import tooltips from '@/components/tooltips.vue'
import grid from '@/components/grid.vue'
import gridItem from '@/components/gridItem.vue'
import searchBar from '@/components/searchBar.vue'
import subscript from '@/components/subscript.vue'

Vue.component('vf-actionsheet', actionsheet)
Vue.component('vf-button', button)
Vue.component('cell-group', cellGroup)
Vue.component('vf-dialog', dialog)
Vue.component('cell', cell)
Vue.component('tooltips', tooltips)
Vue.component('ui-row', grid)
Vue.component('ui-row-item', gridItem)
Vue.component('search-bar', searchBar)
Vue.component('subscript', subscript)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
})



