<template>
    <div class="ui-tooltips" :class="typeClass">
        <div class="ui-tooltips-cnt" :class="classes">
            <i v-if="type || guide || warn"></i>
            <slot></slot>
            <a v-if="close" class="ui-icon-close"></a>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        type: String,
        link: Boolean,
        close: Boolean,
        guide: Boolean,
        warn: Boolean,
        button: Boolean,
        fixed: Boolean,
        border: String
    },
    computed: {
        typeClass () {
            return [
                this.type ? `ui-tooltips-${this.type}` : '',
                this.guide ? `ui-tooltips-guide` : '',
                this.button ? `ui-tooltips-function` : '',
                this.warn ? `ui-tooltips-warn` : '',
                this.fixed ? `ui-tooltips-action` : ''
            ]
        },
        classes () {
            return [
                this.link ? `ui-tooltips-cnt-link` : '',
                this.border ? `ui-border-${this.border}` : ''
            ]
        }
    }
}

</script>
<style lang="scss">
    @import "../assets/base/icons/icon-close";
    @import "../assets/component/tips/tips";
    @import "../assets/component/tips/tooltips";
    @import "../assets/component/tips/poptips";
    @import "../assets/component/tips/newstips";
</style>



