<template>
    <div class="ui-grid" :class="typeClass">
        <ul :class="classes">
            <slot></slot>
        </ul>
    </div>
</template>
<script>
export default {
    props: {
        flex: {
            type: Boolean,
            default: true
        },
        space: Boolean,
        border: String,
        group: String
    },
    computed: {
        typeClass () {
            return [
                this.group === '3' ? 'ui-grid-trisect' : '',
                this.group === '2' ? 'ui-grid-bisect' : '',
                this.space ? 'ui-whitespace' : ''
            ]
        },
        classes () {
            return [
                this.border ? `ui-border-${this.border}` : ''
            ]
        }
    }
}

</script>
<style lang="scss">
    @import "../assets/component/grid/grid";
    @import "../assets/component/grid/grid-icon";
</style>



