<template>
    <transition name="actionsheet-fade">
        <div>
            <div class="ui-translucent-mask" @click="$emit('close')"></div>
            <div class="ui-actionsheet">
                <div class="ui-actionsheet-cnt am-actionsheet-down">
                    <h4 v-if="title">{{title}}</h4>
                    <slot></slot>
                    <div class="ui-actionsheet-split-line"></div>
                    <button @click="$emit('close')"></button>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
export default {
    props: {
        position: {
            type: String,
            default: 'bottom'
        },
        title: {
            type: String,
            default: ''
        },
        content: {
            type: String,
            default: ''
        },
        btn: {
            type: Object
        }
    }
}
</script>

<style lang="scss">
@import "../assets/component/actionsheet/actionsheet";
.actionsheet-fade-leave-active {
    animation-duration: 300ms;
    position: relative;
    z-index: 9999;
}
.actionsheet-fade-leave-to{
    animation-name: bgFadeOut;
    .ui-actionsheet {
        animation-duration: 300ms;
        animation-name: contentBootOut;
    }
}
</style>



